 .i{
     display: flex;
     height: 100vh;
 }

 .i-left
 {
     flex: 1 1;
     display: flex;
     align-items: center;
     justify-content: center;


 }
 @-webkit-keyframes pulse {
     from {
         -webkit-transform : scale(0.5);
                 transform : scale(0.5);
         opacity   : 0.15;
     }
     to {
         -webkit-transform : scale(1);
                 transform : scale(1);
         opacity   : 1;
     }
 }
 @keyframes pulse {
     from {
         -webkit-transform : scale(0.5);
                 transform : scale(0.5);
         opacity   : 0.15;
     }
     to {
         -webkit-transform : scale(1);
                 transform : scale(1);
         opacity   : 1;
     }
 }
 .i-left-wrapper
 {
     padding: 50px;
     height: 50%;
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     -webkit-animation: pulse 5s ;
             animation: pulse 5s ;

 }
 .i-rigth
 {
     flex: 1 1;
     position: relative;
 }
 .i-intro
 {
     font-size: 30px;
     font-weight: 300;

 }
 .i-name
 {
     font-size: 60px;

 }
 .i-title
 {
     height: 50px;
     overflow: hidden ;
 }
.i-title-wrapper
{
    height: 100%;
    -webkit-animation: move 10s 3.5s ease-in-out infinite alternate;
            animation: move 10s 3.5s ease-in-out infinite alternate;
}
@-webkit-keyframes move {
    25%{ -webkit-transform: translateY(-50px); transform: translateY(-50px)}
    50%{-webkit-transform: translateY(-100px);transform: translateY(-100px)}
    75%{-webkit-transform: translateY(-150px);transform: translateY(-150px)}
    100%{-webkit-transform: translateY(-200px);transform: translateY(-200px)}
}
@keyframes move {
    25%{ -webkit-transform: translateY(-50px); transform: translateY(-50px)}
    50%{-webkit-transform: translateY(-100px);transform: translateY(-100px)}
    75%{-webkit-transform: translateY(-150px);transform: translateY(-150px)}
    100%{-webkit-transform: translateY(-200px);transform: translateY(-200px)}
}
 .i-title-item
 {
     height: 50px;
     font-size: 30px;
     font-weight: bold;
     color: #59b256;
     display: flex;
     align-items: center;

 }
 .i-img
 {
     width: 100%;
     height: 100%;
     object-fit: cover;
     position: absolute;
     -webkit-animation: insertImg 2s;
             animation: insertImg 2s;
 }
 @-webkit-keyframes insertImg {
     from { -webkit-transform : translateY(-10em); transform : translateY(-10em) } /* slide down from off-screen */
     to   { -webkit-transform : translateY(0em); transform : translateY(0em) }
 }
 @keyframes insertImg {
     from { -webkit-transform : translateY(-10em); transform : translateY(-10em) } /* slide down from off-screen */
     to   { -webkit-transform : translateY(0em); transform : translateY(0em) }
 }
 
 .i-bg
 {
     -webkit-clip-path: polygon(100% 0%, 100% 54%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
             clip-path: polygon(100% 0%, 100% 54%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
     background-color: #59b256;
     width: 100%;
     height: 100%;
     position: absolute;
     top: 0;
     right : 0;
     -webkit-animation: fastSlide 3.5s;
             animation: fastSlide 3.5s;
 }
 .i-arrow {
     position: absolute;
     box-sizing: border-box;
     height: 2vw;
     width: 2vw;
     border-style: solid;
     border-color: black;
     border-width: 0px 1px 1px 0px;
     -webkit-transform: rotate(45deg);
             transform: rotate(45deg);
     transition: border-width 150ms ease-in-out;
     bottom: 50px;
     margin-left: 45%;
 }

 .i-arrow:hover {
     border-bottom-width: 5px;
     border-right-width: 5px;
 }


 @-webkit-keyframes fastSlide {
     0%   { -webkit-transform: translate(120%) skewX(-30deg) ; transform: translate(120%) skewX(-30deg) ; }
     70%  { -webkit-transform: translate(0%)   skewX(-30deg) ; transform: translate(0%)   skewX(-30deg) ; }
     80%  { -webkit-transform: translate(0%)   skewX(20deg)  ; transform: translate(0%)   skewX(20deg)  ; }
     95%  { -webkit-transform: translate(0%)   skewX(-10deg) ; transform: translate(0%)   skewX(-10deg) ; }
     100% { -webkit-transform: translate(0%)   skewX(0deg)   ; transform: translate(0%)   skewX(0deg)   ; }
 }


 @keyframes fastSlide {
     0%   { -webkit-transform: translate(120%) skewX(-30deg) ; transform: translate(120%) skewX(-30deg) ; }
     70%  { -webkit-transform: translate(0%)   skewX(-30deg) ; transform: translate(0%)   skewX(-30deg) ; }
     80%  { -webkit-transform: translate(0%)   skewX(20deg)  ; transform: translate(0%)   skewX(20deg)  ; }
     95%  { -webkit-transform: translate(0%)   skewX(-10deg) ; transform: translate(0%)   skewX(-10deg) ; }
     100% { -webkit-transform: translate(0%)   skewX(0deg)   ; transform: translate(0%)   skewX(0deg)   ; }
 }

 @media screen and (max-width: 480px)
 {
     .i{
         flex-direction: column;
     }
     .i-left-wrapper
     {
         padding: 10px;
         align-items: center;
         text-align: center;
         height: 100%;
         justify-content: center;

     }
     .i-desc
     {
         display: none;
     }
     .i-arrow
     {
         display: none;
     }

 }
 @media screen  and (max-width:1080px) and (min-height: 1920px) {

     .i
     {
         height: 110vh;
         overflow: hidden;
     }

     .i-img
     {
         top: 300px;
         height: 70vh;
         width: 120%;
     }
     .i-bg
     {
         width: 120%;
         height: 100vh;

     }
 }
 @media screen  and (width:600px) and (height: 800px) {
     .i
     {
         height: 110vh;

     }

     .i-img
     {
         height: 70vh;
         width: 140%;
         margin: -20%;
         top: 100px;
     }
     .i-bg
     {

         width: 120%;
         height: 100vh;
     }

 }





.a
{
    height: 100vh;
    display: flex;
    align-items: center;
    -ms-scroll-chaining: none;
        overscroll-behavior: none;


}

.a-left
{
    flex: 1 1;
