}
.a-card
{
    width: 60%;
    height: 70vh;
    border-radius: 30px ;
    position: relative;
    overflow: hidden;
}
.a-img
{
    width: 100%;
    object-fit: cover;
    margin-left: 40px;
    -webkit-animation: fastSlid 15s ;
            animation: fastSlid 15s ;
}
.a-card.bg{
    position: absolute;
    top: 60px;
    left: 100px;
    background-color: black;
}
.a-award-img
{
