}
.a-award
{
    flex-grow: 1;
}
.a-award-content-1
{
    margin-top: 50px;
    display: flex;
    align-items: center;
    -webkit-animation: fastSlid 15s ;
            animation: fastSlid 15s ;


}
.a-award-content-2
{
    margin-top: 50px;
    display: flex;
    align-items: center;
    -webkit-animation: fastSlid 20s ;
            animation: fastSlid 20s ;
}
.a-award-content-3
{
    margin-top: 50px;
    display: flex;
    align-items: center;
    -webkit-animation: fastSlid 25s ;
            animation: fastSlid 25s ;
}
.a-award-content-4
{
    margin-top: 50px;
    display: flex;
    align-items: center;
    -webkit-animation: fastSlid 30s ;
            animation: fastSlid 30s ;

}
@-webkit-keyframes fastSlid {
    0%   { -webkit-transform: translate(120%) ; transform: translate(120%) ; }
    70%  { -webkit-transform: translate(0%)  ; transform: translate(0%)  ; }
    80%  { -webkit-transform: translate(0%)    ; transform: translate(0%)    ; }
    95%  { -webkit-transform: translate(0%)   ; transform: translate(0%)   ; }
    100% { -webkit-transform: translate(0%)     ; transform: translate(0%)     ; }
}
@keyframes fastSlid {
    0%   { -webkit-transform: translate(120%) ; transform: translate(120%) ; }
    70%  { -webkit-transform: translate(0%)  ; transform: translate(0%)  ; }
    80%  { -webkit-transform: translate(0%)    ; transform: translate(0%)    ; }
    95%  { -webkit-transform: translate(0%)   ; transform: translate(0%)   ; }
    100% { -webkit-transform: translate(0%)     ; transform: translate(0%)     ; }
}

